import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
import orderList from "../../components/order-list/order-list.vue"
import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import bestPaymentPassword from '../../components/best-payment-password/best-payment-password.vue'
export default {
	components:{
		uniCountdown,
		orderList,
		cmdIcon,
		uniPopup,
		bestPaymentPassword
	},
	data() {
		return {
			type:"",
			moneys:"¥875",
			orderMessage:"确认支付",
			src:"../../static/orderPicture/choosePay.svg",
			src1:"../../static/orderPicture/nochoosePay.svg",
		    goodsName:"桃花源禅意民宿商户",
			goodsType:"大床房",
			stayDate:"7月20日入住-7月22日离店",
			stayNum:"3晚*2间",
			orderName:"王彪",
			orderPhone:"18798764235",
			orderDateList:[
				{
					date:"2019.07.20",
					dateMoney:"2*150"
				},
				{
					date:"2019.07.21",
					dateMoney:"2*150"
				},
				{
					date:"2019.07.22",
					dateMoney:"2*150"
				},
				{
					date:"2019.07.20",
					dateMoney:"2*150"
				},{
					date:"2019.07.20",
					dateMoney:"2*150"
				},
			],
			payFlag: false,
			paymentPwd: '' //可以写默认值
		}
	},
	onLoad() {

	},
	methods: {
		togglePopup(type, open) {
			this.type = type;
			this.$refs[open].open();
		},
			
		cancel(type) {
			this.$refs[type].close()
		},
		change(e) {
			console.log(e.show)
		},
		choose(){
			let temp = this.src;
			this.src = this.src1;
			this.src1 = temp;
			
		},
		goPaySuccess(e){
			console.log(e)
			uni.navigateTo({
				url:"paySuccess?money="+e
			})
		},
		togglePayment() {
			this.payFlag = !this.payFlag;
		},
		checkPwd: function(e) {
			console.log(e)
			// 这里是写验证密码的业务逻辑,比如密码错误可以在这边清空
			this.paymentPwd = '';
		},
		forgetPwd:function(){
			uni.navigateTo({
				url:"settingPwd"
			})
		}

	}
}
